import Vue from "vue";
import Vuex from "vuex";
import router from "../router";
import * as umi from "@umi-top/umi-core-js";
import { mnemonicToSeedSync } from "bip39";
import { isIOS, isAndroid, isMacOs, isWindows } from "mobile-device-detect";

const fetch = require("node-fetch");

Vue.use(Vuex);

const stats = endpoint => {
  return new Promise((resolve, reject) => {
    let url = "https://stats.umi.top";
    if (
      location.host.indexOf("testnet") > -1 ||
      // location.host.indexOf("localhost") > -1 ||
      location.host.indexOf("heroku") > -1
    )
      url = "https://stats-testnet.umi.top";

    fetch(url + endpoint)
      .then(res => res.json())
      .then(json => resolve(json.data))
      .catch(err => reject(err));
  });
};

const node = (method, params) => {
  return new Promise((resolve, reject) => {
    let url = "https://snowfall-api.umi.top/api/" + method + "?" + new URLSearchParams(params).toString();

    // url = "http://127.0.0.1:8080/api/" + method + "?" + new URLSearchParams(params).toString();

    fetch(url, {
      method: "POST",
    })
      .then(res => res.json())
      .then(json => resolve(json))
      .catch(err => reject(err));
  });
};

export default new Vuex.Store({
  state: {
    loading: false,
    locale: localStorage.getItem("lang") || "en",
    currency: localStorage.getItem("currency") || "USD",
    cookie: localStorage.getItem("cookieAccepted") || false,
    application: isIOS || isAndroid ? false : true,
    exchangeRates: {
      USD: {
        value: 0,
        limit: 2
      },
      BTC: {
        value: 0,
        limit: 5
      },
      RUB: {
        value: 0,
        limit: 2
      }
    },

    secretKey: null,
    publicKey: null,
    signedMessage: null,
    address: null,
    balance: null,
    special: false,
    isNew: true,
    prefix: "umi",

    addresses: [],
    transactions: [],
    stats: {
      all: 0,
      sended: 0,
      received: 0
    },
    params: {
      limit: 10,
      offset: 0
    },
    filter: "all",

    connected: [],
    structures: []
  },
  getters: {
    locale: state => state.locale,
    loading: state => state.loading,
    isLoggedIn: state => !!state.secretKey,
    signedMessage: state => state.signedMessage,
    address: state => state.address,
    balance: state => state.balance,

    wallet: state => {
      return {
        address: state.address,
        balance: state.balance ? state.balance.confirmed : 0,
        interest: state.balance ? state.balance.interest : 0,
        composite: state.balance ? state.balance.composite : 0,
        special: state.special,
        isNew: state.isNew
      };
    },

    transactions: state => {
      let trxs = state.transactions;
      if (state.filter !== "all")
        trxs = trxs.filter(trx => {
          return state.filter == "sended"
            ? trx.sender === state.address
            : trx.sender !== state.address;
        });
      if (trxs.length > 0)
        return trxs.slice(
          state.params.offset,
          state.params.offset + state.params.limit
        );
      return [];
    },
    stats: state => state.stats,
    params: state => state.params,
    filter: state => state.filter,

    course: state =>
      state.exchangeRates[state.currency]
        ? state.exchangeRates[state.currency].value
        : 0,
    decimal: state =>
      state.exchangeRates[state.currency]
        ? state.exchangeRates[state.currency].limit
        : 2,
    currency: state => state.currency,
    currencies: state => Object.keys(state.exchangeRates) || {},
    cookie: state => state.cookie,
    application: state => state.application,
    apple: () => "https://apps.apple.com/app/umi-wallet/id1527992117",
    google: () =>
      "https://play.google.com/store/apps/details?id=com.umi.umiwallet&hl=",
    isMobile: () => isIOS || isAndroid,
    device: () =>
      isIOS
        ? "iOS"
        : isAndroid
        ? "android"
        : isMacOs
        ? "mac"
        : isWindows
        ? "windows"
        : "other",

    structures: state => state.structures
  },
  mutations: {
    setLocale: (state, locale) => {
      localStorage.setItem("lang", locale);
      state.locale = locale;
    },
    setCurrency(state, value) {
      localStorage.setItem("currency", value);
      state.currency = value;
    },
    setCookie: state => {
      localStorage.setItem("cookieAccepted", true);
      state.cookie = true;
    },
    declineApp: state => {
      state.application = true;
    },
    setMnemonic(state, value) {
      const seed = mnemonicToSeedSync(value.trim());
      state.secretKey = umi.SecretKey.fromSeed(seed);
      state.publicKey = state.secretKey.getPublicKey();
      state.bech32 = umi.Address.fromKey(state.secretKey);
      state.address = state.bech32.getBech32();
      state.structures.map(structure => {
        state.addresses.push(
          state.bech32.setPrefix(structure.prefix).getBech32()
        );
      });
    },
    setBalance(state, payload) {
      state.balance = payload;
      state.special = payload.composite && payload.composite > 0;
    },
    setParams(state, payload) {
      state.params = payload;
    },
    setFilter(state, value) {
      state.filter = value;
    },
    setAddress(state, prefix) {
      state.address = state.bech32.setPrefix(prefix).getBech32();
      state.prefix = prefix;
    },
    setSignedMessage(state, value) {
      if (value) {
        state.signedMessage = umi.base64Encode(
          state.secretKey.sign(umi.textEncode(value))
        );
      }
    },

    setTransactions(state, payload) {
      state.transactions = payload;

      let received = [];
      if (payload.length > 0) {
        state.isNew = false;
        received = payload.filter(trx => trx.recipient === state.address);
        state.stats.all = payload.length;
        state.stats.received = received.length > 0 ? received.length : 0;
        state.stats.sended = state.stats.all - state.stats.received;
      }
      payload.map(trx => {
        let prefix = trx.recipient ? trx.recipient.substr(0, 3) : 'umi';
        if (state.connected.indexOf(prefix) === -1 && umi.Address.fromBech32(trx.recipient) === state.bech32)
          state.connected.push(prefix);
      });
      state.structures.map(str => {
        str.connected = state.connected.includes(str.prefix);
        return str;
      });
    },

    setStructures(state, payload) {
      state.structures = payload.map(str => {
        if (str.prefix === "roy") {
          str.url = "https://roy.club";
          str.description =
            "The biggest crypto-community with more than 150,000 participants.";
        }
        if (str.prefix === "isp") {
          str.url = "https://ispclub.ru";
          str.description =
            "High-tech platform for automatic staking of UMI cryptocurrency up to 5669% per year.";
        }
        str.connected = state.connected.includes(str.prefix);
        return str;
      });
    },

    addToConnected(state, prefix) {
      state.connected.push(prefix);
      state.structures[
        state.structures.findIndex(structures => structures.prefix == prefix)
      ].connected = true;
    },

    setRates(state, payload) {
      state.exchangeRates = payload;
    },

    toDefault(state) {
      state.balance = null;
      state.transactions = [];
      state.stats = {
        all: 0,
        sended: 0,
        received: 0
      };
      state.params = {
        limit: 10,
        offset: 0
      };
      state.filter = "all";
    },

    logout(state) {
      state.address = null;
      state.secretKey = null;
      state.buffer = null;
      state.balance = null;
      state.signedMessage = null;
      state.special = false;
      state.isNew = true;
      state.connected = [];
      state.structures.map(str => {
        str.connected = false;
        return str;
      });
      state.addresses = [];
    }
  },
  actions: {
    changeLocale: ({ commit }, locale) => commit("setLocale", locale),
    changeAddress({ commit, dispatch }, prefix) {
      commit("toDefault");
      commit("setAddress", prefix);
      dispatch("fetchBalance");
      dispatch("fetchTransactions");
      dispatch("fetchStructures");
    },
    changeCurrency({ commit }, value) {
      commit("setCurrency", value);
    },
    login({ commit, dispatch }, value) {
      commit("setMnemonic", value);
      dispatch("fetchStructures");
      dispatch("fetchBalance");
      dispatch("fetchTransactions");
      dispatch("fetchStructures");
      router.push("/wallet");
    },
    signMessage({ commit }, message) {
      commit("setSignedMessage", message);
    },
    logout({ commit }) {
      commit("logout");
      commit("toDefault");
      router.push("/");
    },
    getRates({ commit }) {
      stats("/rates").then(data => {
        commit("setRates", data);
      });
    },
    fetchBalance({ state, commit }) {
      node("getBalance", { address: state.address }).then(data => {
        commit("setBalance", data);
      });
    },
    fetchTransactions({ state, commit, dispatch }) {
      node("listTransactions", { address: state.address }).then(data => {
        if (
          (data && data.length !== state.transactions.length) ||
          state.special ||
          state.prefix == "umi"
        )
          setTimeout(() => dispatch("fetchBalance"), 200);
        commit("setTransactions", data);
      });
      dispatch("checkAddresses");
    },
    fetchStructures({ commit }) {
      node("listStructures").then(data => commit("setStructures", data));
    },
    checkAddresses({ commit, state }) {
      state.addresses.map(address => {
        node("listTransactions", { address: address })
          .then(data => {
            if (data && data.length > 0)
              commit("addToConnected", address.substr(0, 3));
          })
          .catch(e => console.log(e));
      });
    },
    sendTransaction({ state, dispatch }, payload) {
      return new Promise((resolve, reject) => {
        state.loading = true;
        try {
          const value = parseInt(payload.value * 100);

          const recipient = umi.Address.fromBech32(payload.recipient);
          const sender = umi.Address.fromBech32(state.address);

          const tx = new umi.Transaction()
            .setVersion(umi.Transaction.Basic)
            .setSender(sender)
            .setRecipient(recipient)
            .setValue(value)
            .sign(state.secretKey);

          return node("sendTransaction", {
            base64: umi.base64Encode(tx.getBytes())
          })
            .then(data => {
              state.loading = false;
              setTimeout(() => {
                dispatch("fetchBalance");
                dispatch("fetchTransactions");
              }, 3000);
              resolve(data);
            })
            .catch(err => {
              state.loading = false;
              reject(err);
            });
        } catch (err) {
          state.loading = false;
          reject(err);
        }
      });
    }
  }
});
